@import url("https://maxcdn.bootstrapcdn.com/font-awesome/4.4.0/css/font-awesome.min.css");
body {
  margin: 0;
  padding: 0; }

body {
  margin: 0;
  padding: 0; }

.ms-Grid {
  padding: 0 0px;
  height: 100vh; }

.ms-Grid-row {
  margin: 0px 0px; }

.clearfix:after {
  content: ".";
  visibility: hidden;
  display: block;
  height: 0;
  clear: both; }

/* >>>>> MAIN PAGE ELEMENTS <<<<< */
#page-header {
  background-color: #0078d7;
  color: #fff;
  padding-top: 15px;
  padding-bottom: 15px;
  text-align: center;
  width: 100%; }

#page-navigation {
  position: fixed;
  top: 0px;
  z-index: 2000; }

.mail-bar {
  margin-top: 60px; }

#page-main {
  text-align: left;
  padding-right: 25px;
  padding-left: 25px;
  padding-bottom: 120px; }

.page-title {
  margin-top: 10px;
  margin-bottom: 10px; }

#page-footer {
  background-color: #005a9e;
  margin-top: 25px;
  padding-top: 15px;
  padding-bottom: 15px;
  text-align: center;
  position: fixed;
  bottom: 0px; }

.mail-list {
  overflow-y: scroll;
  height: auto; }

.mail-item {
  position: relative; }

.header-element-base, #brand-logo, #brand-name, #site-tagline {
  display: inline-block; }

#brand-name {
  margin-left: 15px; }

#site-tagline {
  margin-left: 75px;
  width: 360px;
  text-align: left; }

/* global navigation */
nav {
  margin: 0;
  background: #005a9e;
  text-align: right; }

#global-nav {
  list-style: none; }
  #global-nav li {
    display: inline-block; }
    #global-nav li a:hover {
      background: #004578; }
  #global-nav a {
    text-decoration: none;
    display: block;
    text-align: center;
    padding: 10px;
    color: #fff; }
  #global-nav li:last-of-type a {
    border-bottom: none; }

/* local navigation */
#local-nav {
  margin: 15px 50px 0px 0px;
  text-align: right; }
  #local-nav ul {
    list-style: none; }
    #local-nav ul a {
      text-decoration: none;
      display: block;
      padding: 0.5em; }
  #local-nav li:last-of-type a {
    border-bottom: none; }

/* table utils */
.align-middle {
  vertical-align: middle; }

.no-wrap-cell {
  white-space: nowrap; }

/* icon page tweaks */
.icon-list ul li {
  float: left;
  font-size: 11px;
  color: black;
  width: 15%;
  text-align: center;
  height: 100px;
  padding: 10px;
  overflow: hidden; }
  .icon-list ul li .ms-Icon {
    font-size: 35px;
    display: block;
    margin: 10px auto; }

/* animation page tweaks */
.animation-container {
  position: relative;
  width: 100%;
  display: inline-block;
  overflow: hidden; }

.animation-content {
  height: 100%;
  border: 1px solid gray;
  padding: 10px; }

.animation-panel-in {
  position: absolute;
  top: 0px;
  width: 60px;
  height: 100%;
  opacity: 0; }

.animation-panel-out {
  position: absolute;
  top: 0px;
  width: 60px;
  height: 100%;
  opacity: 1; }

.ms-Persona {
  padding-top: 5px; }

.ms-Persona-details {
  font-family: 'Segoe UI Semilight WestEuropean','Segoe UI Semilight','Segoe WP Semilight','Segoe UI','Segoe WP',Tahoma,Arial,sans-serif; }

/* -------- NG-WIG -------- */
/**
 *
 *  RESET BOX MODEL
 *
 */
.ng-wig,
[class^="nw-"] {
  -o-box-sizing: border-box;
  -ms-box-sizing: border-box;
  box-sizing: border-box; }

/**
 *   main wrapper for the editor
 *
 *  .ng-wig
 *
 */
.ng-wig {
  display: block;
  padding: 0;
  margin: 0; }

/**
 *  styling for toolbar and its items
 *
 *  .nw-toolbar
 *    &__item
 *
 */
.nw-toolbar {
  display: block;
  margin: 0 !important;
  padding: 0 !important;
  list-style: none !important;
  font-size: 12px;
  color: #6B7277;
  background: -webkit-linear-gradient(90deg, #ffffff 0%, #f9f9f9 100%);
  background: -webkit-linear-gradient(270deg, #ffffff 0%, #f9f9f9 100%);
  background: linear-gradient(180deg, #ffffff 0%, #f9f9f9 100%);
  border: 1px solid #CCCCCC;
  border-radius: 3px 3px 0 0; }

.nw-toolbar__item {
  display: inline-block;
  vertical-align: top;
  margin: 0;
  border-right: 1px solid #DEDEDE; }

.nw-toolbar label {
  line-height: 30px;
  display: inline-block;
  padding: 0 6px 0 3px; }

.nw-toolbar input[type=checkbox] {
  vertical-align: -3px;
  margin-right: -1px; }

/**
 *  styling for the editor part: source code (original textarea) and resulting div
 *
 *  .nw-editor
 *    &__src
 *    &__res
 *
 */
.nw-editor {
  display: table;
  /* Default when height is not set */
  height: 300px;
  background: #fff;
  cursor: text;
  width: 100%; }

.nw-editor-container {
  border: 1px solid #CCCCCC;
  border-top: none;
  border-radius: 0 0 3px 3px;
  position: relative; }

.nw-editor__res {
  min-height: 100%;
  padding: 0 8px;
  display: table-cell; }

.nw-editor__src,
.nw-editor__res {
  width: 100%;
  outline: none;
  box-sizing: border-box;
  border: none;
  margin: 0; }

.nw-editor__src-container {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0; }

.nw-editor__src {
  height: 100%;
  resize: none;
  padding: 0 8px; }

.nw-editor--fixed .nw-editor {
  display: block;
  overflow-y: auto; }

.nw-editor--fixed .nw-editor__res {
  padding: 1px 8px;
  display: block; }

.nw-invisible {
  visibility: hidden; }

.nw-editor--fixed .nw-invisible {
  display: none; }

/**
 *  styling for toolbar button, has two modifiers: active and type of icon for background
 *
 *  .nw-button
 *    &--active
 *    &--{button type}
 *
 */
.nw-button {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  display: block;
  width: 30px;
  height: 30px;
  margin: 0;
  padding: 0;
  opacity: 0.5;
  background-color: transparent;
  background-position: center center;
  background-repeat: no-repeat;
  border: none;
  border-radius: 2px;
  font-size: 0;
  cursor: pointer; }

.nw-button:before {
  font-size: 12px;
  font-family: FontAwesome; }

.nw-button.bold:before {
  content: '\f032'; }

.nw-button.italic:before {
  content: '\f033'; }

.nw-button.list-ul:before {
  content: '\f0ca'; }

.nw-button.list-ol:before {
  content: '\f0cb'; }

.nw-button.link:before {
  content: '\f0c1'; }

.nw-button.nw-button--source:before {
  content: '\f040'; }

.nw-button:focus {
  outline: none; }

.nw-button:hover,
.nw-button.nw-button--active {
  opacity: 1; }

.nw-button--active {
  background-color: #EEEEEE; }

/**
 *  styling & formatting of content inside contenteditable div
 *
 *  .nw-content
 *
 */
.nw-content {
  padding: 12px;
  margin: 0;
  font-family: sans-serif;
  font-size: 14px;
  line-height: 24px; }

.nw-select {
  height: 30px;
  padding: 6px;
  color: #555;
  background-color: inherit;
  border: 0; }

.nw-select:focus {
  outline: none; }

